body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}


.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.NAVIGATION{list-style:none;display:flex;margin-left:-20px}.NAVIGATION__item{margin-right:20px}.NAVIGATION__item a{color:#000;font-size:14px;border-bottom:2px solid transparent;-webkit-transition:all 400ms;transition:all 400ms}.NAVIGATION__item a:hover{text-decoration:none;border-bottom:2px solid #000}.NAVIGATION .active{border-bottom:2px solid #000}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.LOGO{padding:10px;box-sizing:border-box}.LOGO img{width:100%}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.MENU{background:linear-gradient(101deg, #e3664a 0%, #a02b0f 100%);padding-bottom:2px}.MENU__container{background-color:#fff;padding:0 5%;display:flex;justify-content:space-between;align-items:center}.MENU__container-logo{width:100px}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.INPUT{color:#2b2b2e;border:none;border-radius:4px;width:100%;padding:5px 20px;-webkit-transition:all 200ms;transition:all 200ms}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.BUTTON{background-color:#fff;border-radius:4px;padding:5px 20px;-webkit-transition:all 200ms;transition:all 200ms;border:none}.BUTTON:active,.BUTTON:hover{-webkit-transform:translate3d(-5px, -5px, -5px);transform:translate3d(-5px, -5px, -5px);box-shadow:10px 10px 0px -3px rgba(0,0,0,0.75)}.BUTTON.rev{border:1px solid #000}.BUTTON.search{width:40%}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.IMAGE-CARD{width:350px;height:440px;margin:40px auto 0 auto;border-radius:4px;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.75);-webkit-transition:all 200ms;transition:all 200ms}.IMAGE-CARD:hover{box-shadow:10px 10px 30px 0px rgba(0,0,0,0.75)}@media screen and (max-width: 575px){.IMAGE-CARD{width:100%;height:auto}}.IMAGE-CARD__imageContainer{overflow:hidden}.IMAGE-CARD__imageContainer-image{width:100%;height:300px;background-position:center;background-repeat:no-repeat;background-size:cover;cursor:pointer;-webkit-transition:all 200ms;transition:all 200ms}.IMAGE-CARD__imageContainer-image:hover{-webkit-transform:scale(1.2);transform:scale(1.2);opacity:0.7}.IMAGE-CARD__textContainer{background-color:#fff;padding:20px;color:#2b2b2e;font-size:14px}.IMAGE-CARD__textContainer .creator,.IMAGE-CARD__textContainer .like-count,.IMAGE-CARD__textContainer .description{margin:10px 0 0 0}.IMAGE-CARD__textContainer .creator{text-overflow:ellipsis;overflow:hidden;white-space:nowrap;font-size:18px}@media screen and (max-width: 575px){.IMAGE-CARD__textContainer .creator{text-overflow:unset;overflow:unset;white-space:unset}}.IMAGE-CARD__textContainer .creator span{font-weight:bold;color:#e3664a}.IMAGE-CARD__textContainer .likes span{color:#e3664a}.IMAGE-CARD__textContainer .description{text-overflow:ellipsis;overflow:hidden;white-space:nowrap}@media screen and (max-width: 575px){.IMAGE-CARD__textContainer .description{text-overflow:unset;overflow:unset;white-space:unset}}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.SIDEBAR{position:fixed;top:120px;left:-100%;border-top-right-radius:4px;border-bottom-right-radius:4px;background-color:#fff;padding:20px;-webkit-transition:all 200ms;transition:all 200ms;z-index:999;box-shadow:0px 0px 10px 0px rgba(0,0,0,0.75)}@media screen and (max-width: 768px){.SIDEBAR{top:110px;max-width:768px}}.SIDEBAR:hover{-webkit-transform:translateX(10px);transform:translateX(10px)}@media screen and (max-width: 768px){.SIDEBAR__saved-container{display:flex;flex-wrap:wrap}}.SIDEBAR__saved-container-item .item{-webkit-transition:all 200ms;transition:all 200ms;margin-top:10px;cursor:pointer}.SIDEBAR__saved-container-item .item:hover{color:#50c389}@media screen and (max-width: 768px){.SIDEBAR__saved-container-item .item{margin:0 5px}}.SIDEBAR__saved-container-item .delete{float:right;color:#dd4242;cursor:pointer}

.LOADER{position:fixed;top:calc(50% - 100px);left:calc(50% - 100px);z-index:999}.LOADER img{width:200px}

.SECTION{padding:40px 0}.SECTION-FIRST{padding:0;margin:0}.SEARCH{min-height:80vh}.SEARCH__background{background:rgba(255,255,255,0.8);background-image:-webkit-gradient(linear, left top, right top, from(rgba(255,255,255,0.1)), to(rgba(255,255,255,0.3))),url(/React-unsplash-api/static/media/header.27275d0d.jpg);background-image:linear-gradient(to right, rgba(255,255,255,0.1), rgba(255,255,255,0.3)),url(/React-unsplash-api/static/media/header.27275d0d.jpg);background-position:center;background-repeat:no-repeat;background-size:cover;height:50vh}.SEARCH__header{width:100%}.SEARCH__header-title{text-align:center}.SEARCH__header-title h1{font-size:28px}.SEARCH__header-input{width:80%;margin:0 auto}.SEARCH__header-err{background-color:rgba(255,255,255,0.7);padding:10px;width:30%;text-align:center;margin:10px auto 0;border:1px solid #dd4242}.SEARCH__header-err span{cursor:pointer;color:#dd4242;float:right}.SEARCH__header-buttons{width:80%;margin:40px auto 0;display:flex;justify-content:space-between}@media screen and (max-width: 320px){.SEARCH__header-buttons{flex-flow:wrap;margin:20px auto 0}.SEARCH__header-buttons .BUTTON{margin:20px auto 0}}.SEARCH__container-pagination{margin:20px 0 0 0;width:100%;display:flex;justify-content:space-between}@media screen and (max-width: 575px){.SEARCH .rowfix{display:block}}

